import React from 'react'
import { Box, Grid, Hidden, Typography } from '@material-ui/core'
import { AppShell } from 'components/AppShell'
import { CreatePost } from 'components/createPost/CreatePost'
import { FollowingList } from 'components/followingList/FollowingList'
import { PostsList } from 'components/postsList/PostsList'
import { useLoggedUser } from 'components/user/userHooks'
import i18n from 'i18n'

export const WallScreen = () => {
  const loggedUser = useLoggedUser()

  return (
    <AppShell header={{ title: i18n.home }}>
      <Grid container justify="center" spacing={3}>
        <Grid item xs style={{ maxWidth: '600px' }}>
          <Box py={2}>
            <CreatePost />
          </Box>
          <PostsList
            filter={{ authorUserId: [loggedUser.id, ...loggedUser.followingUserIds] }}
            empty={() => <FollowingList filter={{ userId: loggedUser.id }} />}
          />
        </Grid>
        <Hidden smDown>
          <Grid item md={4}>
            <Typography variant="h6">{i18n.following}</Typography>
            <FollowingList filter={{ userId: loggedUser.id }} canEdit hideButton maxHeight={300} />
          </Grid>
        </Hidden>
      </Grid>
    </AppShell>
  )
}
