import React from 'react'
import { useTheme } from '@material-ui/core'
import { styles as avatarStyles } from '@material-ui/core/Avatar/Avatar'
import Skeleton from '@material-ui/lab/Skeleton'
import { PostShell } from 'components/post/PostShell'
import { getRandomNumber } from 'mock/mockUtils'

export const PostLoading = (props) => {
  const theme = useTheme()
  return (
    <PostShell
      avatar={() => (
        <Skeleton variant="circle" width={avatarStyles(theme).root.width} height={avatarStyles(theme).root.height} />
      )}
      name={() => <Skeleton variant="text" width={getRandomNumber(60, 120)} />}
      handle={() => <Skeleton variant="text" width={getRandomNumber(60, 120)} />}
      text={() => <Skeleton variant="text" width={`${getRandomNumber(30, 90)}%`} />}
    />
  )
}
