import { getDb, matchAll, USERS_COUNT } from 'mock/mockUtils'
import { createPost } from 'mock/postMock'
import { createUser } from 'mock/userMock'

export const init = () => {
  const db = getDb()
  db.users = [...Array(USERS_COUNT).keys()].map((i) => {
    const id = i + 1
    return createUser({
      id,
      // followingUserIds: id === 1 ? [] : [getRandomNumber(2, USERS_COUNT)].filter((num) => num !== id),
      followingUserIds: [],
    })
  })
  db.posts = db.users.reduce((result, { id }) => {
    return [...result, ...[...Array(5).keys()].map((i) => createPost({ id: i + result.length + 1, authorUserId: id }))]
  }, [])
  console.log('🛎 ', 'db', db)
}

export const findUsers = ({ fulltext, ...params } = {}) => {
  let users = matchAll(getDb().users, params)
  if (fulltext) {
    users = users.filter((user) => {
      const fields = [user.firstName, user.lastName, user.handle].map((field) => field.toLowerCase())
      return fields.some((field) => field.includes(fulltext.toLowerCase()))
    })
  }
  return users
}

export const findFollowers = ({ id } = {}) => {
  return matchAll(getDb().users, { id })[0]
}

export const findPosts = ({ authorUserId } = {}) => {
  const posts = matchAll(getDb().posts, { authorUserId })
  const userPosts = posts.map((post) => {
    const author = findUsers({ id: post.authorUserId }).pop()
    return {
      ...post,
      author, // "JOIN" post with user
    }
  })
  userPosts.sort((a, b) => (a.createdTime > b.createdTime ? -1 : 1))
  return userPosts
}

export const savePost = (post) => {
  getDb().posts.push({ ...post, id: getDb().posts.length + 1, createdTime: new Date() }) // Mutate
  return { success: true }
}

export const updateUser = ({ id, ...values }) => {
  const user = findUsers({ id })[0]
  Object.assign(user, values) // Mutate
  return { success: true }
}
