import React from 'react'
import { Avatar, Typography, Link } from '@material-ui/core'
import { Link as LinkRoute } from 'react-router-dom'
import { PostShell } from 'components/post/PostShell'
import { getProfileUrl } from 'components/user/userUtils'

export const Post = (props) => {
  const name = [props.author.firstName, props.author.lastName].filter(Boolean).join(' ')
  return (
    <PostShell
      avatar={() => <Avatar src={props.author.avatar} />}
      name={() => (
        <Link to={getProfileUrl(props.author)} component={LinkRoute} color="inherit">
          <Typography component="strong" style={{ fontWeight: 600 }}>
            {name}
          </Typography>
        </Link>
      )}
      handle={() => (
        <Typography role="link" aria-label="handle">
          @{props.author.handle}
        </Typography>
      )}
      text={() => <Typography>{props.text}</Typography>}
    />
  )
}
