import React from 'react'
import { AppShell } from 'components/AppShell'
import { CustomError } from 'components/error/CustomError'
import { userContext } from 'components/user/UserContext'
import { useUser } from 'components/user/userHooks'

export const UserProvider = ({ children }) => {
  const loggedUser = useUser({ id: 1 })
  const { Provider } = userContext

  if (loggedUser.isLoading) {
    return <AppShell />
  }

  if (loggedUser.isError) {
    throw new CustomError('Logged user not found', { heading: 'Ooops', text: 'Logged user not found' })
  }

  return <Provider value={loggedUser.data}>{children}</Provider>
}
