import { rest } from 'msw'
import { findUsers, findPosts, findFollowers, updateUser, savePost } from 'mock/db'
import { paramsToInt, getBody, response } from 'mock/mockUtils'

export default [
  rest.post(
    '*/users',
    response((req) => findUsers(getBody(req)))
  ),

  rest.put(
    '*/users',
    response((req) => updateUser(getBody(req)))
  ),

  rest.post(
    '*/posts',
    response((req) => findPosts(getBody(req)))
  ),

  rest.put(
    '*/posts',
    response((req) => savePost(getBody(req)))
  ),

  rest.get(
    '*/followers/:id',
    response((req) => findFollowers(paramsToInt(req.params)))
  ),
]
