import React from 'react'
import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core'
import { ErrorBoundary } from 'react-error-boundary'
import { CustomError } from 'components/error/CustomError'
import i18n from 'i18n'

export default ({ children }) => {
  const onReset = () => {
    window.location = '/'
  }
  const onError = () => {
    console.info('Sending error to external logging service.') // I would use Sentry or something similar
  }
  return (
    <ErrorBoundary FallbackComponent={getFallbackComponent} onReset={onReset} onError={onError}>
      {children}
    </ErrorBoundary>
  )
}

const getFallbackComponent = ({ error, resetErrorBoundary }) => {
  let heading = i18n.somethingWentWrong
  let text = i18n.reloadPage
  if (error instanceof CustomError) {
    heading = error.props.heading
    text = error.props.text
  }
  return (
    <Dialog
      open
      disableBackdropClick
      disableEscapeKeyDown
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      maxWidth="xs"
      fullWidth
    >
      <DialogTitle id="alert-dialog-title">{heading}</DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">{text}</DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={resetErrorBoundary} autoFocus>
          {i18n.reload}
        </Button>
      </DialogActions>
    </Dialog>
  )
}
