import React from 'react'
import {
  Avatar,
  Box,
  Button,
  List,
  ListItem,
  ListItemIcon,
  ListItemSecondaryAction,
  ListItemText,
  Typography,
} from '@material-ui/core'
import { useHistory } from 'react-router-dom'
import { useLoggedUser, useUpdate } from 'components/user/userHooks'
import { getFullName, getProfileUrl } from 'components/user/userUtils'
import i18n from 'i18n'

export const UsersList = (props) => {
  const loggedUser = useLoggedUser()
  const update = useUpdate()
  const history = useHistory()

  const toggleFollow = (userId) => () => {
    let followingUserIds = loggedUser.followingUserIds
    if (isFollowing(userId)) {
      followingUserIds = followingUserIds.filter((id) => id !== userId)
    } else {
      followingUserIds = [...loggedUser.followingUserIds, userId]
    }
    update({ followingUserIds })
  }
  const isFollowing = (userId) => loggedUser.followingUserIds.includes(userId)

  if (!props.users.length) {
    return (
      <Box display="flex" justifyContent="center" my={3}>
        {props.emptyMessage || i18n.emptyUsers}
      </Box>
    )
  }

  return (
    <List style={{ width: '100%' }}>
      {props.users.map((user) => (
        <ListItem key={user.id} onClick={() => history.push(getProfileUrl(user))} style={{ cursor: 'pointer' }}>
          <ListItemIcon>
            <Avatar src={user.avatar} />
          </ListItemIcon>
          <ListItemText>
            <Typography>{getFullName(user)}</Typography>
            <Typography>@{user.handle}</Typography>
          </ListItemText>
          {!props.hideButton && (
            <ListItemSecondaryAction>
              <Button variant="outlined" onClick={toggleFollow(user.id)}>
                {isFollowing(user.id) ? i18n.unfollow : i18n.follow}
              </Button>
            </ListItemSecondaryAction>
          )}
        </ListItem>
      ))}
    </List>
  )
}
