import React from 'react'
import { Box, Button } from '@material-ui/core'
import { Link } from 'react-router-dom'
import { useFollowing } from 'components/followingList/followingListHooks'
import { UsersList } from 'components/user/UsersList'
import i18n from 'i18n'

export const FollowingList = (props = {}) => {
  const following = useFollowing(props.filter)

  if (following.isLoading || !following.data) {
    return ''
  }

  return (
    <Box display="flex" flexDirection="column" width={1} alignItems="center">
      <Box width={1} maxHeight={props.maxHeight} overflow="scroll">
        <UsersList users={following.data} emptyMessage={props.emptyMessage} hideButton={props.hideButton} />
      </Box>
      {props.canEdit && (
        <Box display="flex" justifyContent="center" marginTop={1}>
          <Button variant="contained" to="/search-people" component={Link} aria-label="add people">
            {i18n.addPeople}
          </Button>
        </Box>
      )}
    </Box>
  )
}
