import { useUser, useUsers } from 'components/user/userHooks'

export const useFollowing = (params = {}) => {
  const user = useUser({ id: params.userId })
  const users = useUsers({ id: user.data && user.data.followingUserIds }, { manual: true })
  if (user.data && !users.data) {
    users.refetch()
  }
  return users
}
