import React from 'react'
import { AppBar, Avatar, IconButton, makeStyles, Toolbar, Typography } from '@material-ui/core'
import { Link } from 'react-router-dom'
import { useLoggedUser } from 'components/user/userHooks'
import { getProfileUrl } from 'components/user/userUtils'

export const Header = (props) => {
  const classes = useStyles()
  const loggedUser = useLoggedUser()

  return (
    <>
      <AppBar position="fixed" color="default">
        <Toolbar>
          {props.leftButton && <props.leftButton />}
          <Typography variant="h6" className={classes.title}>
            {props.title}
          </Typography>
          {loggedUser && (
            <IconButton
              to={getProfileUrl(loggedUser)}
              color="inherit"
              component={Link}
              role="link"
              aria-label="logged user"
            >
              <Avatar src={loggedUser.avatar} />
            </IconButton>
          )}
        </Toolbar>
      </AppBar>
    </>
  )
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  leftButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}))
