import React from 'react'
import { useParams } from 'react-router-dom'
import { AppShell } from 'components/AppShell'
import { CustomError } from 'components/error/CustomError'
import { FollowingList } from 'components/followingList/FollowingList'
import { ButtonBack } from 'components/header/ButtonBack'
import { useLoggedUser, useUser } from 'components/user/userHooks'
import i18n from 'i18n'

export const FollowScreen = () => {
  const loggedUser = useLoggedUser()
  let { handle } = useParams()
  const user = useUser({ handle })

  if (user.isError) {
    throw new CustomError(user.error.message, { heading: i18n.userNotFound, text: i18n.userNotFoundDescription })
  }

  return (
    <AppShell header={{ leftButton: ButtonBack, title: i18n.following }}>
      {user.data && (
        <FollowingList
          filter={{ userId: user.data.id }}
          canEdit={user.data.id === loggedUser.id}
          emptyMessage={user.data.id === loggedUser.id ? i18n.notFollowingAnybodySelf : i18n.notFollowingAnybody}
        />
      )}
    </AppShell>
  )
}
