export const db = {}
export const USERS_COUNT = 10
export const MAX_DELAY = 1000

// Returns global variable
export const getDb = () => {
  if (!window.__lookiterDb) window.__lookiterDb = {}
  return window.__lookiterDb
}

// Checks if all keys in params are equal in the resource
// In case of array it matches ir=f at least one element matches
export const matchAll = (resource, params) => {
  return resource.filter((r) => {
    return !Object.keys(params).some((key) => {
      if (params[key] === undefined) return false
      if (Array.isArray(params[key])) {
        return !params[key].includes(r[key])
      }
      return r[key] !== params[key]
    })
  })
}

export const paramsToInt = (params) => {
  return Object.keys(params).reduce((acc, key) => {
    acc[key] = parseInt(params[key])
    return acc
  }, {})
}

export const getRandomNumber = (min, max) => {
  return Math.floor(Math.random() * (max - min) + min)
}

// in node the body is string but in browser it's an object
export const getBody = (req) => {
  let body = req.body
  try {
    body = JSON.parse(req.body)
  } catch (e) {}
  return body
}

export const response = (fn) => (req, res, ctx) => {
  return new Promise((resolve) => {
    setTimeout(() => resolve(res(ctx.json(fn(req, res, ctx)))), getDelay())
  })
}

const getDelay = () => getRandomNumber(5, MAX_DELAY)
