import React from 'react'
import produce from 'immer'
import { queryCache, useMutation, useQuery } from 'react-query'
import { userContext } from 'components/user/UserContext'

export const useLoggedUser = () => {
  const loggedUser = React.useContext(userContext)
  return loggedUser
}

export const useUsers = (params = {}, options = {}) => {
  return useQuery(
    ['users', params],
    async (key, body) => {
      const response = await fetch('/users', {
        method: 'POST',
        body: JSON.stringify(body),
        headers: {
          'Content-Type': 'application/json',
        },
      })
      const users = await response.json()
      if (options.unique) {
        if (users.length !== 1) {
          throw new Error('Error fetching user')
        }
        return users[0]
      }
      return users
    },
    {
      enabled: !options.manual,
    }
  )
}

export const useUser = (params, options = {}) => {
  return useUsers(params, { ...options, unique: true })
}

export const useUpdate = () => {
  const loggedUser = useLoggedUser()
  const queryKey = ['users', { id: loggedUser.id }]
  const userPostsQueryKey = ['posts', { authorUserId: [loggedUser.id, ...loggedUser.followingUserIds] }]

  const [mutate] = useMutation(
    async (updatedValues) => {
      const body = {
        id: loggedUser.id,
        ...updatedValues,
      }
      const response = await fetch('/users', {
        method: 'PUT',
        body: JSON.stringify(body),
        headers: {
          'Content-Type': 'application/json',
        },
      })
      return response.json()
    },
    {
      onMutate: (updatedValues) => {
        queryCache.cancelQueries(queryKey)

        const loggedUserData = queryCache.getQueryData(queryKey)

        const loggedUserUpdated = produce(loggedUserData, (draft) => {
          Object.assign(draft, updatedValues)
        })

        queryCache.setQueryData(queryKey, loggedUserUpdated)

        return { loggedUserData }
      },
      // On failure, roll back to the previous value
      onError: (error, variables, { loggedUserData }) => {
        queryCache.setQueryData(queryKey, loggedUserData)
      },
      // After success or failure, refetch
      onSettled: () => {
        queryCache.invalidateQueries(queryKey, { refetchActive: false })
        queryCache.invalidateQueries(userPostsQueryKey)
      },
    }
  )
  return mutate
}
