import React from 'react'
import { Post } from 'components/post/Post'
import { usePosts } from 'components/postsList/postsListHooks'
import { PostsListLoading } from 'components/postsList/PostsListLoading'
import i18n from 'i18n'

export const PostsList = (props = {}) => {
  const posts = usePosts(props.filter)

  if (posts.isLoading) {
    return <PostsListLoading />
  }

  if (posts.data.length === 0) {
    if (props.empty) {
      return props.empty()
    }
    return i18n.noPosts
  }

  return (
    <>
      {posts.data.map((post) => (
        <Post key={post.id} {...post} />
      ))}
    </>
  )
}
