import React from 'react'
import { Box, TextField } from '@material-ui/core'
import { PostsListLoading } from 'components/postsList/PostsListLoading'
import { useLoggedUser, useUsers } from 'components/user/userHooks'
import i18n from 'i18n'
import { UsersList } from './UsersList'

export const SearchUsers = () => {
  const loggedUser = useLoggedUser()
  const [text, setText] = React.useState('')
  const users = useUsers({ fulltext: text }, { manual: true })
  const { refetch } = users

  const search = ({ target }) => {
    setText(target.value)
  }

  React.useEffect(() => {
    if (!text) return
    refetch()
  }, [text, refetch])

  return (
    <Box display="flex" flexDirection="column" width={1} alignItems="center">
      <TextField
        value={text}
        onChange={search}
        label={i18n.searchPeopleInput}
        role="textbox"
        aria-label={i18n.searchPeopleInput}
      />
      {users.isLoading || (text && !users.data) ? (
        <Box width={1}>
          <PostsListLoading />
        </Box>
      ) : (
        text && <UsersList users={users.data.filter((user) => user.id !== loggedUser.id)} />
      )}
    </Box>
  )
}
