import React from 'react'
import { Avatar, Box, Button, TextareaAutosize } from '@material-ui/core'
import { useCreatePost } from 'components/createPost/createPostHooks'
import { useLoggedUser } from 'components/user/userHooks'
import i18n from 'i18n'

export const CreatePost = (props) => {
  const loggedUser = useLoggedUser()
  const createPost = useCreatePost()
  const [text, setText] = React.useState('')

  const submit = () => {
    createPost(text)
    setText('')
  }

  const onTextChange = ({ target }) => {
    setText(target.value)
  }

  React.useEffect(() => {}, [text])

  return (
    <Box display="flex">
      <Box display="flex" justifyContent="center" width="80px">
        <Avatar src={loggedUser.avatar} />
      </Box>

      <Box width={1}>
        <TextareaAutosize
          value={text}
          rowsMax={3}
          aria-label="Create post"
          placeholder={i18n.whatsHappening}
          onChange={onTextChange}
          style={{ width: '100%', padding: '10px 0px 10px 0px', border: 'none' }}
        />

        <Box display="flex" justifyContent="flex-end">
          <Button variant="outlined" onClick={submit} disabled={!text} role="button" aria-label="publish">
            {i18n.post}
          </Button>
        </Box>
      </Box>
    </Box>
  )
}
